*{
    margin: 0px;
    padding: 0px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; 
    
    
}

.divmain{
    margin: 60px;
    border-radius: 5px;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.633);
    box-shadow: 5px 8px 8px rgba(0, 0, 0, 0.422);
    
   
}
.tit{
    margin-bottom: 20px;
    
}

.resultado{
    margin: 50px;

}

.moeda{
    padding: 20px;
    border-radius: 5px;
    margin: 50px;

}

.div1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.div1 > input{
    margin: 19px;
    text-align: center;
    border-radius: 5px;
    width: 500px;
    height: 30px;
    font-size: 2rem;
    background-color: rgb(184, 182, 182);
}
.div1 > input:hover{
    background-color: rgb(133, 133, 133);
}
.btn{
    padding: 20px;
    border-radius: 5px;
    box-shadow: 3px 5px 5px rgba(0, 0, 0, 0.422);
    background-color: rgb(158, 158, 0);

}
.btn:hover{
     background-color: rgb(229, 229, 3);
}

.resul{
    display: flex;
    justify-content: center;
    align-items: center;
}
.resultado{
    font-size: 3rem;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(158, 158, 0);
}

@media (max-width : 600px) {
    .div1{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .div1 > input{
        text-align: center;
        border-radius: 5px;
        max-width: 250px;
        height: 30px;
        font-size: 2rem;
        background-color: rgb(184, 182, 182);
    }
    .btn{
        margin: 5px;
        padding: 10px;
        border-radius: 5px;
        box-shadow: 3px 5px 5px rgba(0, 0, 0, 0.422);
        background-color: rgb(158, 158, 0);
        width: 40px;
    
    }
    .moeda{
        padding: 10px;
        border-radius: 5px;
        margin: 10px;
        margin-top: 20px;
        width: 60px;
    
    }
}
